import type {FC} from 'react'
import React, {useCallback} from 'react'

import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'

import {CommonOpenEventButton} from './CommonOpenEventButton'
import type {OpenEventButtonCommonProps} from './OpenEventButton'


export const AdcOpenEventButton: FC<OpenEventButtonCommonProps> = ({eventId, onClick}) => {
    const setActivePage = useNavigationState('setActivePage')

    const handleOpen = useCallback(() => {
        if (eventId) {
            setActivePage({name: AppRoutes.Calendar})
        }

        onClick()
    }, [eventId, onClick, setActivePage])

    return (
        <CommonOpenEventButton onClick={handleOpen} />
    )
}
